import {Menu, NewColor, Colors} from "./containers";
import '../../stylesheets/APP.scss'
import {Route, Switch} from "react-router-dom";
import Color from "./ui/Color";

const App = () =>
  <Switch>
    <Route exact path="/:id" component={Color}/>
    <Route path="/" component={() =>
      <div className="app">
        <Menu/>
        <NewColor/>
        <Colors/>
      </div>
    }/>
  </Switch>;

export default App;


